import React from "react";
import { TEXT_COLOR } from "../util/constants";

const AHref = ({
  text,
  url,
  underline = true,
}: {
  text: string;
  url: string;
  underline?: boolean;
}) => {
  return (
    <a
      href={url}
      target={"_blank"}
      rel="noreferrer"
      style={{
        color: TEXT_COLOR,
        textDecoration: `${!underline ? "none" : ""}`,
      }}
    >
      {text}
    </a>
  );
};

export default AHref;
