import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';

/* 
    Add name of the component name in below list to use its shader from this slice.
*/
export type Components = 'Loader' | 'IntroName' | 'Image';

export interface ShaderType {
	vertex: string;
	fragment: string;
}
export type ShaderSliceType = {
	[k in Components]: ShaderType | null;
} & { loading: boolean };

const initialState: ShaderSliceType = {
	loading: true,
	IntroName: null,
	Image: null,
	Loader: null,
};

export const shaderSlice = createSlice({
	name: 'shaderSlice',
	initialState,
	reducers: {},
	extraReducers(builder) {
		builder
			.addCase(load_shader.pending, (state) => {
				state.loading = true;
			})
			.addCase(load_shader.fulfilled, (state, action) => {
				if (action.payload) {
					state[action.payload.component] = {
						fragment: action.payload.fragment,
						vertex: action.payload.vertex,
					};
				}
			});
	},
});

export const load_shader = createAsyncThunk(
	'shader/loader',
	async (
		{
			for_component,
			fragment_url,
			vertex_url,
		}: {
			for_component: Components;
			vertex_url: string;
			fragment_url: string;
		},
		{ rejectWithValue }
	) => {
		try {
			const vert = await (await fetch(vertex_url)).text();
			const frag = await (await fetch(fragment_url)).text();
			return {
				component: for_component,
				vertex: vert,
				fragment: frag,
			};
		} catch (error) {
			rejectWithValue(error);
		}
	}
);

// Action creators are generated for each case reducer function
// export const {} = shaderSlice.actions;

export default shaderSlice.reducer;
