import React from "react";
import { BACKGROUND_COLOR, TEXT_COLOR } from "../util/constants";

const MobilePage = () => {
  return (
    <div
      id="mobile_device"
      style={{
        fontFamily: "Poppins",
        fontWeight: "bolder",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        color: TEXT_COLOR,
        height: "100vh",
        width: "80vw",
        padding: "0 10vw",
        zIndex: 110,
        position: "fixed",
        backgroundColor: BACKGROUND_COLOR,
        top: 0,
      }}
    >
      <p>
        This website is not supported on mobile, please use a desktop or laptop.
      </p>
    </div>
  );
};

export default MobilePage;
