import React from "react";
import useScript from "../hooks/useScript";

const LorenzAttractor = () => {
  useScript("https://threejs.org/build/three.js");
  useScript("/js/LorenzAttractor.js");
  useScript("/js/LorenzAttractorRender.js", true);

  return (
    <div id="lorenzAttractorHolder">
      <style>
        {`p ,h4{
        margin: 0%;
        }`}
      </style>
      <div
        className={`info ${
          window.location.href.includes("Lorenz-Attractor")
            ? "updatedInfoMargins"
            : ""
        }`}
      >
        <h4>Parameters</h4>
        <p>
           = 10 <br />
           = 28 <br />
           = 8/3 <br />
          <b>Time Step</b> = 0.01
          {window.location.href.includes("Lorenz-Attractor") && (
            <h4>
              By{" "}
              <a className="githubLink" href="https://github.com/anand-kamble">
                Anand Kamble
              </a>
            </h4>
          )}
        </p>
      </div>
      <div className="instructions">
        Left Click and drag to rotate. <br />
        Right click and drag to pan. <br />
        Scroll to zoom.
        <br />
        <button id="resetSimulation" className="reset-btn">
          Restart Simulation
        </button>
      </div>
    </div>
  );
};

export default LorenzAttractor;
